body, html {
    -webkit-font-smoothing: antialiased !important;
    -moz-osx-font-smoothing: grayscale;
    overflow-y: auto;
    overflow-x: hidden;
    font-family: 'Montserrat', sans-serif;
}

@media (min-width: 1200px) {
    .container {
        width: 960px;
    }
}

h1 {
    font-weight: 700;
    font-size: 46px;
}

h2 {
    text-transform: uppercase;
    font-size: 20px;
}

h3 {
    font-size: 14px;
    font-style: italic;
    font-weight: 100;
    line-height: 26px;
}

h4 {
    text-transform: uppercase;
    font-size: 16px;
    word-spacing: 2px;
}

h5 {
    text-transform: uppercase;
    font-size: 12px;
    word-spacing: 2px;
}

h6 {
    text-transform: uppercase;
    font-size: 10px;
    word-spacing: 2px;
    letter-spacing: 2px;
}

p {
