}

#home {
    background: url(../img/bg1.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: auto;
    min-height: 710px;
    padding-top: 60px;
    color: #ffffff;
}

#home a.down-btn span.fa.fa-angle-down {
    font-size: 26px;
    border: 1px solid #c59a6d;
    border-radius: 2px;
    padding: 7px 5px;
    position: relative;
    margin-top: 100px;
    color: #ffffff;
    cursor: pointer;
}

.content {
    padding-top: 16%;
}

.header-text.btn {
    border: 1px solid #ffffff;
    border-radius: 0;
    padding: 10px 30px;
    color: #e6e6e6;
}

.header-text.btn:hover {
    color: inherit;
}

.header-text.btn:focus,
.header-text.btn:active:focus,
.header-text.btn.active:focus,
.header-text.btn.focus,
.header-text.btn:active.focus,
.header-text.btn.active.focus {
    outline: thin dotted transparent;
    outline: none;
    outline-offset: -2px;
}

.typed-cursor {
    opacity: 1;
    padding: 10px 2px;
    background: #ffffff;
    margin: 10px;
    -webkit-animation: blink 0.5s linear infinite;
    -moz-animation: blink 0.5s linear infinite;
    animation: blink 0.5s linear infinite;
}

@keyframes blink {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes blink {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-moz-keyframes blink {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

span.typed-cursor {
    position: relative;
    font-family: Montserrat;
    font-size: 46px;
    color: #e6e6e6;
    display: -o-flex;

}


