    font-family: 'Montserrat', sans-serif;
}

address:before {
    content: "\f041";
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: 20px;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #c59a6d;
    vertical-align: middle;
    margin-right: 15px;
    margin-left: -30px;
}

address {
