}

#contact .form-control {
    display: block;
    width: 600px;
    height: 40px;
    padding: 7px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: #1e2429;
    background-image: none;
    border: 0;
    border-top: 2px solid transparent;
    border-radius: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    transition: all 0.5s;
}

#contact .form-control:focus {
    border-color: #C59A6D;
    outline: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
}

